.rp-nav-screen {
  top: calc(var(--rp-nav-height)  + var(--rp-banner-height, 0px));
  background-color: var(--rp-c-bg);
  width: 100%;
  z-index: var(--rp-z-index-nav-screen);
  padding: 24px 48px;
  display: none;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  overflow-y: auto;
}

.rp-nav-screen--open {
  display: block;
}

.rp-nav-screen__container {
  flex-direction: column;
  align-items: center;
  gap: 2px;
  display: flex;
}

.rp-nav-screen__others {
  display: flex;
}

@media (width <= 1280px) {
  .rp-nav-screen__others {
    display: none;
  }
}

.rp-nav-screen-divider {
  background-color: var(--rp-c-divider-light);
  width: 100%;
  height: 1px;
  margin: 1rem 0;
}

