.rp-steps {
  border-left: 1px solid var(--rp-c-text-3);
  counter-reset: step;
  margin-bottom: 44px;
  margin-left: 16px;
  padding-left: 24px;
}

.rp-steps h3 {
  counter-increment: step;
  margin-top: 3px;
  position: relative;
}

.rp-steps h3:before {
  content: counter(step);
  width: 30px;
  height: 30px;
  color: var(--rp-c-text-1);
  background-color: var(--rp-code-block-bg);
  border: 1.5px solid var(--rp-c-text-3);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-size: 1.1rem;
  font-weight: 400;
  line-height: 30px;
  display: flex;
  position: absolute;
  left: -41px;
}

.rp-steps p {
  color: var(--rp-c-text-1);
}

