.rp-nav-screen-versions {
  border-radius: var(--rp-radius-small);
  justify-content: space-between;
  align-items: center;
  width: 100%;
  min-height: 36px;
  padding: 0 12px;
  font-size: 14px;
  display: flex;
}

.rp-nav-screen-versions__left {
  align-items: center;
  display: flex;
}

.rp-nav-screen-versions__right {
  align-items: center;
  gap: 4px;
  display: flex;
}

.rp-nav-screen-versions__icon {
  transition: transform .3s;
  transform: rotate(-90deg);
}

.rp-nav-screen-versions__icon--open {
  transform: rotate(0);
}

.rp-nav-screen-versions-group {
  flex-direction: column;
  align-items: flex-end;
  gap: 8px;
  width: 100%;
  height: 0;
  font-size: 14px;
  transition: height .3s;
  display: flex;
  overflow: hidden;
}

.rp-nav-screen-versions-group__item {
  padding: 0 24px;
}

.rp-nav-screen-versions-group__item--active {
  color: var(--rp-c-text-2);
  cursor: default;
  pointer-events: none;
}

.rp-nav-screen-versions-group--open {
  height: auto;
}

