:root {
  --rp-outline-padding-x: 20px;
}

.rp-outline {
  border-left: 1px solid var(--rp-c-divider-light);
  flex-direction: column;
  display: flex;
}

.rp-outline__title {
  padding-left: var(--rp-outline-padding-x);
  padding-right: var(--rp-outline-padding-x);
  height: 32px;
  color: var(--rp-c-text-1);
  align-items: center;
  gap: 4px;
  margin-bottom: 4px;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 32px;
  display: inline-flex;
}

@media (width <= 1280px) {
  .rp-outline__title {
    display: none;
  }
}

.rp-outline__divider {
  margin-left: var(--rp-outline-padding-x);
  margin-right: var(--rp-outline-padding-x);
  background: var(--rp-c-divider-light);
  height: 1px;
  margin-top: 16px;
  margin-bottom: 16px;
}

.rp-outline__toc {
  padding-left: var(--rp-outline-padding-x);
  padding-right: var(--rp-outline-padding-x);
  flex-direction: column;
  flex: 1;
  display: flex;
}

@media (width <= 1280px) {
  .rp-outline__toc {
    max-height: 60vh;
    overflow: auto scroll;
  }
}

.rp-outline__bottom {
  padding-left: var(--rp-outline-padding-x);
  padding-right: var(--rp-outline-padding-x);
}

