.rp-sidebar-section-header {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  padding: 4px 12px;
  display: flex;
}

.rp-sidebar-section-header:first-child {
  margin-top: 0;
}

.rp-sidebar-section-header__left {
  color: var(--rp-c-text-1);
  align-items: center;
  font-size: 14px;
  font-weight: 600;
  display: flex;
}

.rp-sidebar-section-header__right {
  align-items: center;
  display: flex;
}

